h1 {
  display: inline-block;
  font-size: 30px;
  margin-top: 15px;
}

h1 img {
  display: inline-block;
  vertical-align: text-bottom;
  width: 50px;
  height: 36px;
}

nav {
  margin-top: 20px;
  font-size: 20px;
  margin-right: 25px;
  display: inline-block;
  float: right;
}

a {
  color: white;
  text-decoration: none;
  margin-left: 20px;
}

a:hover {
  color: red;
  cursor: pointer;
}


#engname {
  color: red;
  font-style: oblique;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-weight: bold;
}



footer img{
  display: inline-block;
  vertical-align:text-bottom;
  margin-right: 3px;
  width: 25px;
  height: 25px;
}

footer {
  margin-top: 0px;
  display: flex;
  justify-content: flex-end;
  margin-right: 20px;
  
}

footer a {
  display: inline-block;
}

footer p {
  display: inline-block;
  margin-bottom: 10px;
}


.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  
}

#card{
  display: inline-block;
  border: 10px solid black;
  border-radius: 10px;
  width: 400px;
  height: 400px; 
  margin-left: 150px;
  margin-top: 30px;
}


.project {
  display: flex;
  flex-wrap: wrap;
}

#texts {
  margin-left: 16px;
  margin-top:10px;
}

@media screen and (min-width:1520px) {

  #card{
    
    width: 400px;
    height: 400px; 

  }
  

}